import { useEffect, useState, useContext } from "react";
import axios from "axios";
import { MainContext } from "../contexts/MainContext";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";


const HeaderComponent = () => {

  const navigate = useNavigate()
  const [lost, setLost] = useState(0);
  const { t } = useTranslation()
  const { compare, setCompare, removeCompare } = useContext(MainContext);

  const [user, setUser] = useState("");
  const [pass, setPass] = useState("");

  const [loading, setLoading] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {

      if (compare.length) {

        const elem = document.querySelector(".compare-listing");

        if (elem) {
          elem.classList.remove("hidden");
          elem.classList.add("listing-open");
        }

      } else {

          const elem = document.querySelector(".compare-listing");

          if (elem) {
            elem.classList.add("hidden");
            elem.classList.remove("listing-open");
          }

      }

  }, [compare])



  const onSubmit = async () => {
    if (loading) return false;

    setLoading(true);

    try {
      const { data } = await axios.post("/api/qsyrys/login", {
        email: user,
        password: pass,
      });

      localStorage.setItem("user", data.user);

      localStorage.setItem("token", data.token);

      document.querySelector(".modal.modal-login").classList.remove("show");

      setLoading(false);
    } catch (err) {
      console.log(err);

      setError(
        err?.response?.data.message ||
          "Something went wrong, please try again later"
      );

      setLoading(false);
    }
  };

  useEffect(() => {
    document
      .querySelector(".action.prevent-default")
      .addEventListener("click", (e) => {
        e.preventDefault();

        document
          .querySelector("#search_popup_wrapper")
          .classList.remove("dialog--open");
      });

    document.querySelector("button.close").addEventListener("click", (e) => {
      e.preventDefault();

      document.querySelector(".modal.modal-login").classList.remove("show");
    });
  }, []);

  return (
    <>
      <div className="mobile-nav-overlay"></div>

      <div
        className="modal modal-login fade pr-[17px]"
        id="ere_signin_modal"
        tabIndex="-1"
        aria-modal="true"
        role="dialog"
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <button
              type="button"
              className="close !text-black !text-[30px]"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true"></span>
            </button>
            {!Boolean(lost) && (
              <div className="ere-login-wrap">
                {Boolean(error.length) && (
                  <div className="ere_messages message text-center">
                    {error}
                  </div>
                )}

                <form
                  className="ere-login"
                  onSubmit={(e) => {
                    e.preventDefault();
                    return false;
                  }}
                  encType="multipart/form-data"
                  noValidate="novalidate"
                >
                  <div className="form-group control-username">
                    <input
                      value={user}
                      onChange={(e) => setUser(e.target.value)}
                      name="user_login"
                      className="form-control control-icon login_user_login"
                      placeholder={t("user_placeholder")}
                      type="text"
                    />
                  </div>
                  <div className="form-group control-password">
                    <input
                      onChange={(e) => setPass(e.target.value)}
                      value={pass}
                      name="user_password"
                      className="form-control control-icon"
                      placeholder={t("password_placeholder")}
                      type="password"
                    />
                  </div>
                  {/* <div className="checkbox mr-3">
                      <label>
                        <input name="remember" type="checkbox" />
                        Remember me{" "}
                      </label>
                    </div> */}

                  <a
                    href="javascript:void(0)"
                    onClick={() => setLost(1)}
                    className="ere-reset-password my-[15px]"
                  >
                    {t("lost_password")}
                  </a>
                  <button
                    type="submit"
                    onClick={onSubmit}
                    className="ere-login-button btn btn-primary btn-block"
                  >
                    {!loading ? t("login") : t("loading")}
                  </button>
                </form>
                <hr />
              </div>
            )}
            {Boolean(lost) && (
              <>
                {lost == 1 && (
                  <div className="ere-reset-password-wrap">
                    <div className="ere-resset-password-wrap">
                      <div className="ere_messages message ere_messages_reset_password"></div>
                      <form
                        onSubmit={(e) => {
                          e.preventDefault();
                          return false;
                        }}
                        encType="multipart/form-data"
                      >
                        <div className="form-group control-username">
                          <input
                            name="user_login"
                            className="form-control control-icon reset_password_user_login"
                            placeholder={t("user_placeholder")}
                          />
                          <button
                            type="submit"
                            className="btn btn-primary btn-block ere_forgetpass"
                          >
                            {t("new_password")}
                          </button>
                        </div>
                      </form>
                    </div>
                    <a
                      href="javascript:void(0)"
                      onClick={() => setLost(0)}
                      className="ere-back-to-login"
                    >
                      {t("go_login")}
                    </a>
                  </div>
                )}

                {lost == 3 && (
                  <div className="ere-reset-password-wrap">
                    <div className="ere-resset-password-wrap">
                      <div className="ere_messages message ere_messages_reset_password"></div>
                      <form
                        onSubmit={(e) => {
                          e.preventDefault();
                          return false;
                        }}
                        method="post"
                        encType="multipart/form-data"
                      >
                        <div className="form-group control-username">
                          <input
                            type="password"
                            name="newpassword"
                            className="form-control control-icon reset_password_user_login"
                            placeholder={t("new_password_placeholder")}
                          />

                          <input
                            name="renewpassword"
                            className="form-control control-icon reset_password_user_login"
                            placeholder={t("renew_password_placeholder")}
                          />

                          <button
                            type="submit"
                            className="btn btn-primary btn-block ere_forgetpass"
                          >
                            {t("reset_password")}
                          </button>
                        </div>
                      </form>
                    </div>
                    <a
                      href="javascript:void(0)"
                      onClick={() => setLost(0)}
                      className="ere-back-to-login"
                    >
                      {t("go_login")}
                    </a>
                  </div>
                )}
              </>
            )}{" "}
          </div>
        </div>
      </div>

      <div id="compare-listings" className="compare-listing hidden">
        <div className="compare-listing-header">
          <h4 className="title"> {t("compare")}</h4>
        </div>{" "}
        <div id="compare-properties-listings">
          <div className="compare-listing-body">
            <div className="compare-thumb-main row">
              <div
                className="compare-thumb compare-property"
                data-property-id="771"
              >
                {compare.length && (
                  <img
                    className="compare-property-img"
                    width="150"
                    height="150"
                    src={compare[compare.length - 1].image}
                    onError={(e) => {
                      e.target.src =
                        "https://www.qsyrys.com/wp-content/uploads/2017/12/no-image.jpg";
                    }}
                    decoding="async"
                    loading="lazy"
                  />
                )}
                <button
                  onClick={() => removeCompare()}
                  type="button"
                  className="compare-property-remove"
                >
                  <i className="fa fa-times"></i>
                </button>
              </div>
            </div>

            <button
              type="button"
              onClick={() => {
                navigate("/compare");
              }}
              disabled={compare.length < 2}
              className="btn btn-primary btn-xs compare-properties-button"
            >
              {t("compare")}
            </button>
          </div>

          <button
            type="button"
            onClick={() => {
              const elem = document.querySelector("#compare-listings");
              const btnElem = document.querySelector(".listing-btn i");

              if (elem.classList.contains("listing-open")) {
                elem.classList.remove("listing-open");

                btnElem.classList.remove("fa-angle-right");
                btnElem.classList.add("fa-angle-left");
              } else {
                elem.classList.add("listing-open");

                btnElem.classList.add("fa-angle-right");
                btnElem.classList.remove("fa-angle-left");
              }
            }}
            className="btn btn-primary listing-btn"
          >
            <i className="fa fa-angle-right"></i>
          </button>
        </div>
      </div>

      <div id="search_popup_wrapper" className="dialog dialog--close">
        <div className="dialog__overlay"></div>
        <div className="dialog__content">
          <div className="morph-shape">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="100%"
              height="100%"
              viewBox="0 0 520 280"
              preserveAspectRatio="none"
            >
              <rect x="3" y="3" fill="none" width="516" height="276"></rect>
            </svg>
          </div>
          <div className="dialog-inner">
            <h2>{t("search_placeholder")}</h2>
            <form method="get" action="/search" className="search-popup-inner">
              <input
                type="text"
                name="s"
                placeholder={t("search_placeholder_main")}
              />
              <button
                className="bt bt-sm bt-background text-[#624615] zm:!relative bt-primary"
                type="submit"
              >
                {t("search")}
              </button>
            </form>
            <div>
              <a
                className="action prevent-default"
                data-dialog-close="close"
                href="#"
              >
                <i className="fa fa-close transition03"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default HeaderComponent;
